<template>
    <div>
        <div class="cm-panel" v-if="!!baseInfo && (!!baseInfo.hasFeeDiscount || isFeeProd) && !!feeDiscountInfo">
            <h3 class="margin-b-14"></h3>
            <Form ref="feeDiscountInfo" :model="feeDiscountInfo"
                  :label-width="120">
                <Row>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="">
                            <span class="spanline32">{{feeDiscountInfo.poundageAmount|amount}}</span>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="">
                            <span class="spanline32">{{feeDiscountInfo.poundageModeName}}</span>
                        </Form-item>
                    </i-col>
                </Row>
                <Row v-if="!!discountInfo && !!discountInfo.hasFeeRule">
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="">
                            <span class="spanline32">{{feeDiscountInfo.isDiscountName}}</span>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isFeeDiscount&&isFeeDealerDiscount">
                        <FormItem label="">
                            <span class="spanline32">{{feeDiscountInfo.dealerDiscountAmount|amount}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isFeeDiscount&&!!isFeeManufacturerDiscount">
                        <FormItem label="">
                            <span class="spanline32">{{feeType}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isFeeDiscount&&!!isFeeManufacturerDiscount">
                        <FormItem label="">
                            <span class="spanline32">{{feeDiscountInfo.manufacturerDiscountAmount|amount}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isFeeDiscount&&!!isFeeManufacturerDiscount">
                        <FormItem label="(%)">
                            <span class="spanline32">{{feeDiscountInfo.ratioValue|amount}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isFeeDiscount">
                        <FormItem label="">
                            <span class="spanline32">{{feeDiscountInfo.discountTotalAmount|amount}}</span>
                        </FormItem>
                    </i-col>
                </Row>
            </Form>
        </div>
        <div class="cm-panel"
             v-if="!!baseInfo && !!baseInfo.hasRateDiscount && !!discountInfo && !!discountInfo.hasRateRule && !!rateDiscountInfo">
            <h3 class="margin-b-14"></h3>
            <Form ref="rateDiscountInfo" :model="rateDiscountInfo" :label-width="120">
                <Row>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="">
                            <span class="spanline32">{{rateDiscountInfo.isDiscountName}}</span>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isRateDiscount&&!!isRateDealerDiscount">
                        <FormItem label="">
                            <span class="spanline32">{{rateDiscountInfo.dealerDiscountAmount|amount}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isRateDiscount&&!!isRateManufacturerDiscount">
                        <FormItem label="">
                            <span class="spanline32">{{discountType}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isRateDiscount&&!!isRateManufacturerDiscount">
                        <FormItem label="">
                            <span class="spanline32">{{rateDiscountInfo.manufacturerDiscountAmount|amount}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isRateDiscount&&!!isRateManufacturerDiscount">
                        <FormItem label="(%)">
                            <span class="spanline32">{{rateDiscountInfo.ratioValue|amount}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isRateDiscount">
                        <FormItem label="">
                            <span class="spanline32">{{rateDiscountInfo.discountTotalAmount|amount}}</span>
                        </FormItem>
                    </i-col>
                </Row>
            </Form>
        </div>
    </div>
</template>
<script>
    /**
     * @Description: 
     */
    import base from './js/loanbase.js';
    import TRANS from '@/views/order/edit/loan/js/trans.js';
    import {yx} from '@/utils/helper.js';

    export default {
        name: 'discountInfo',
        mixins: [base],
        props: {},
        components: {},
        directives: {},
        data() {
            return {
                isFeeProd: false,//"011100001" "011100002"---isShowPoundage
                isFeeDealerDiscount: 0, //
                isFeeManufacturerDiscount: 0, //
                isRateDealerDiscount: 0, //
                isRateManufacturerDiscount: 0, //
                allDisabled: true,
                discountType:"",
                feeType:''
            }
        },
        computed: {
            //
            isFeeDiscount() {
                return !!this.baseInfo && !!this.feeDiscountInfo && this.feeDiscountInfo.isDiscount == 1;
            },
            //
            isRateDiscount() {
                return !!this.baseInfo && !!this.rateDiscountInfo && this.rateDiscountInfo.isDiscount == 1;
            },
            //051400000   , 051400001  051400002
            //,
        },
        mounted() {
            //"011100001" "011100002"--isShowPoundage
            this.isFeeProd = this.baseInfo.isShowPoundage == 1;
            //  
            const hasDiscount = this.isFeeProd || this.baseInfo.hasFeeDiscount == 1 || this.baseInfo.hasRateDiscount == 1;
            if (hasDiscount && this.financePeriodYear > 0) {
                this.reloadDiscountInfo();
            } else this.allDisabled = true;
        },
        methods: {
            //
            //
            reloadDiscountInfo(reloadFlag) {
                this.showLoading();
                let url = polarisserver + '/finance/query/financeDiscountInfo';
                let baseInfo = this.baseInfo;
                let orderinfo = this.orderData.orderinfo;
                //
                let params = {
                    "orderNo": this.$route.params.orderNo,
                    "productCode": orderinfo.productCode,
                    "version": orderinfo.productVersion,
                    "term": baseInfo.financePeriod,
                    "masterBrandId": orderinfo.carMasterBrandId,
                    "brandId": orderinfo.carBrandId,
                    "styleId": orderinfo.carStyleId,
                    "modelId": orderinfo.carModelId,
                    "isShowPoundage": this.baseInfo.isShowPoundage,
                }
                this.$http.post(url, params).then(res => {
                    this.hideLoading();
                    let result = res.data;
                    if (!result.success || !result.data) return this.errorModal(result.errorMessage || '');
                    let data = result.data;
                    this.discountInfo = TRANS.transToForm('discountInfo', data);
                    this.feeDiscountInfo = this.discountInfo.feeDiscountInfo;
                    this.rateDiscountInfo = this.discountInfo.rateDiscountInfo;
                    if (!!this.feeDiscountInfo) {
                        //
                        this.isFeeDealerDiscount = this.feeDiscountInfo.isDealerDiscount == 1;
                        //
                        this.isFeeManufacturerDiscount = this.feeDiscountInfo.isManufacturerDiscount == 1;
                    }
                    if(this.rateDiscountInfo){
                        let discountType = this.rateDiscountInfo.discountMode !== "051400002"?this.rateDiscountInfo.discountMode:this.rateDiscountInfo.customDiscountType||"051400000"
                        this.discountType = discountType ==='051400000'?'':''
                    }

                    if(this.feeDiscountInfo){
                        let feeType = this.feeDiscountInfo.discountMode !== "051400002"?this.feeDiscountInfo.discountMode:this.feeDiscountInfo.customDiscountType||"051400000"
                        this.feeType = feeType ==='051400000'?'':''
                    }
                    if (!!this.rateDiscountInfo) {
                        //isRateDealerDiscount
                        this.isRateDealerDiscount = this.rateDiscountInfo.isDealerDiscount == 1;
                        //
                        this.isRateManufacturerDiscount = this.rateDiscountInfo.isManufacturerDiscount == 1;
                    }
                    this.setLoanData({
                        name: 'discountInfo',
                        data: this.discountInfo
                    })
                    this.allDisabled = false;
                    this.$nextTick(() => {
                        this.todoRecount();
                    })
                }).catch(err => {
                    this.hideLoading();
                    this.errorCatchModal('', err)
                    this.allDisabled = true;
                })
            },
        },
    }
</script>



