<template>
    <div>
        <div v-if="baseInfo">
            <h3 class="margin-b-14"></h3>
            <Form ref="baseInfo" :model="baseInfo" :label-width="120" v-if="baseInfo">
                <Row>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="">
                            <span class="spanline32">{{baseInfo.businessTypeName}}</span>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="">
                            <span class="spanline32">{{baseInfo.leaseTypeName}}</span>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="">
                            <span class="spanline32">{{baseInfo.repaymentModeName}}</span>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="">
                            <span class="spanline32">{{baseInfo.repaymentFrequencyName}}</span>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="">
                            <span class="spanline32">{{baseInfo.financePeriodName}}</span>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="">
                            <span class="spanline32">{{baseInfo.isResourceCarName}}</span>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="">
                            <span class="spanline32">{{baseInfo.carTypeName}}</span>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <FormItem label="()">
                            <span class="spanline32">{{baseInfo.carGuidancePrice|amount}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="!isSecondhand">
                        <FormItem label="()">
                            <span class="spanline32">{{baseInfo.actualSellingPrice|amount}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="">
                            <span class="spanline32">{{baseInfo.purchasePurposeName}}</span>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="">
                            <span class="spanline32">{{baseInfo.carCommercialTypeName}}</span>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <FormItem label="">
                            <span class="spanline32">{{baseInfo.predictLicenseProvinceCodeName}}{{!!baseInfo.predictLicenseCityCodeName?'/':''}}{{baseInfo.predictLicenseCityCodeName}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="">
                            <span class="spanline32">{{baseInfo.custPrimaryChannelName}}</span>
                        </Form-item>
                    </i-col>
                    <!--7 start-->
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isSecondhand">
                        <FormItem label="()">
                            <span class="spanline32">{{baseInfo.mileage|amount}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isSecondhand">
                        <FormItem label="()">
                            <span class="spanline32">{{baseInfo.carEstimatedPrice|amount}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isSecondhand">
                        <FormItem label="(VIN)">
                            <span class="spanline32">{{baseInfo.vinNum}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isSecondhand">
                        <FormItem label="">
                            <span class="spanline32">{{ baseInfo.productionDate ? new Date(baseInfo.productionDate).format('yyyy-MM-dd') : ''}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isSecondhand">
                        <FormItem label="">
                            <span class="spanline32">{{baseInfo.initialDate ? new Date(baseInfo.initialDate).format('yyyy-MM-dd') : ''}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isSecondhand">
                        <FormItem label="">
                            <span class="spanline32">
                                {{baseInfo.provinceCodeName}}{{baseInfo.cityCodeName}}{{baseInfo.licenseSequence}}
                            </span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isSecondhand">
                        <FormItem label="">
                            <span class="spanline32">{{baseInfo.carEvaluationAgencyName}}</span>
                        </FormItem>
                    </i-col>
                    <!--7 end-->

                    <!--2 start-->
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isChedidai">
                        <FormItem label="">
                            <span class="spanline32">{{baseInfo.transferTimes}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isChedidai">
                        <FormItem label="">
                            <span class="spanline32">{{baseInfo.compulsoryInsuranceExpiryDate}}</span>
                        </FormItem>
                    </i-col>
                    <!--2 end-->

                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="">
                            <span class="spanline32">{{baseInfo.isReverseMortgageName}}</span>
                        </Form-item>
                    </i-col>
                    <!---->
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isReverseMortgage && !isSecondhand">
                        <FormItem label="">
                            <span class="spanline32">{{baseInfo.carInvoiceDate ? new Date(baseInfo.carInvoiceDate).format('yyyy-MM-dd') : ''}}</span>
                        </FormItem>
                    </i-col>
                    <!---->
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isReverseMortgage && isSecondhand">
                        <FormItem label="">
                            <span class="spanline32">{{baseInfo.carTransferDate ? new Date(baseInfo.carTransferDate).format('yyyy-MM-dd') : ''}}</span>
                        </FormItem>
                    </i-col>
                </Row>
            </Form>
        </div>
    </div>
</template>
<script>
    /**
     * @Description: 
     */
    import base from './js/loanbase.js';

    export default {
        mixins: [base],
        name: 'orderLoanBasicForm',
        props: {},
        components: {},
        data() {
            return {}
        },
        computed: {},
        mounted() {
            //loan   
            this.$nextTick(() => {
                this.todoRecount();
            })
        },
        methods: {},
    }
</script>



