<template>
    <div>
        <div class="cm-panel" v-if="!!insuranceInfo">
            <h3 class="margin-b-14"></h3>
            <Form ref="insuranceInfo" :model="insuranceInfo" :label-width="120"
                  v-if="insuranceInfo">
                <Row>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="">
                            <span class="spanline32">{{insuranceInfo.isFinanceInsurance=='1'?'':''}}</span>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isBuyInsurance">
                        <FormItem label="">
                            <span class="spanline32">{{insuranceInfo.insuranceChannelName}}</span>
                        </FormItem>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-if="isBuyInsurance">
                        <FormItem label="">
                            <span class="spanline32">{{insuranceInfo.insuranceModeName}}</span>
                        </FormItem>
                    </i-col>
                </Row>
                <!---->
                <div class="ivu-table-wrapper input-edit-table table-only-horizon-line" v-if="isBuyInsurance">
                    <div class="ivu-table">
                        <div class="ivu-table-body">
                            <table cellspacing="0" cellpadding="0" border="0" style="width: 100%;">
                                <tbody class="ivu-table-tbody">
                                <tr>
                                    <th width="180">
                                        <div class="ivu-table-cell"><span></span></div>
                                    </th>
                                    <th width="180">
                                        <div class="ivu-table-cell"><span></span></div>
                                    </th>
                                    <th>
                                        <div class="ivu-table-cell"><span></span></div>
                                    </th>
                                    <th width="180">
                                        <div class="ivu-table-cell"><span>()</span></div>
                                    </th>
                                </tr>
                                <tr class="ivu-table-row" v-for="(obj,i) in insuranceInfo.insuranceItemList" :key="i"
                                    v-if="parseInt(obj.period)<=financePeriodYear || obj.financeCode.length>0">
                                    <td width="180">
                                        <div class="ivu-table-cell">
                                            <span>{{yearName(i)}}</span>
                                        </div>
                                    </td>
                                    <td width="180">
                                        <div class="ivu-table-cell">
                                            <span>{{getInsuranceClassText(obj.financeCode,i)||'--'}}</span>
                                        </div>
                                    </td>
                                    <td width="180">
                                        <div class="ivu-table-cell">
                                            <span>{{getInsuranceKindText(obj.insuranceKind)||'--'}}</span></div>
                                    </td>
                                    <td width="180">
                                        <div class="ivu-table-cell">
                                            <Form-item>
                                                <div class="ivu-table-cell">
                                                    <span v-if="obj.insuranceAmount!==''&&obj.insuranceAmount!==undefined&&obj.insuranceAmount!==null">{{obj.insuranceAmount|amount}}</span>
                                                    <span v-else>--</span>
                                                </div>
                                            </Form-item>
                                        </div>
                                    </td>
                                </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </Form>
        </div>
        <div class="cm-panel" v-if="!!gpsInfo">
            <h3 class="margin-b-14">GPS</h3>
            <Form ref="gpsInfo" :model="gpsInfo" :label-width="120">
                <Row>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="GPS">
                            <span class="spanline32">{{gpsInfo.gpsSupplierName}}</span>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="GPS">
                            <span class="spanline32">{{gpsInfo.basePrice|amount}}</span>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="GPS">
                            <span class="spanline32">{{gpsInfo.increasePrice|amount}}</span>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="GPS">
                            <span class="spanline32">{{gpsInfo.increaseFinanceTypeName}}</span>
                        </Form-item>
                    </i-col>
                </Row>
            </Form>
        </div>
        <div class="cm-panel" v-if="!!theftInsuranceInfo">
            <h3 class="margin-b-14"></h3>
            <Form ref="theftInsuranceInfo" :model="theftInsuranceInfo"
                  :label-width="120">
                <Row>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="">
                            <span class="spanline32">{{theftInsuranceInfo.isFinanceInsurance=='1'?'':''}}</span>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6"   v-if="isBuyTheftInsurance && isOnline">
                        <Form-item label="">
                            <span class="spanline32">{{!theftInsuranceInfo.isGift?'':theftInsuranceInfo.isGift=='1'?'':''}}</span>
                        </Form-item>
                    </i-col>
                </Row>
                <Row v-if="isGift">
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="">
                            <span class="spanline32">{{theftInsuranceInfo.increaseFinancePeriodName}}</span>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="">
                            <span class="spanline32">{{theftInsuranceInfo.increasePriceTypeName}}</span>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="">
                            <span class="spanline32">{{theftInsuranceInfo.basePrice|amount}}</span>
                        </Form-item>
                    </i-col>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6">
                        <Form-item label="">
                            <span class="spanline32">{{theftInsuranceInfo.increasePrice|amount}}</span>
                        </Form-item>
                    </i-col>
                </Row>
            </Form>
        </div>
        <div class="cm-panel" v-if="otherFinanceInfo&&otherFinanceInfo.length">
            <h3 class="margin-b-14"></h3>
            <Form ref="otherFinanceInfo" :model="itemInfo" :label-width="120">
                <Row>
                    <i-col :xs="24" :sm="12" :md="8" :lg="6" v-for="(obj,i) in itemInfo.otherFinanceInfo" :key="i"
                           v-if="!isDisabledItem(obj) && !(isGpsIncreaseBasePriceCode(obj.financeCode)&& isGpsIncreaseItemDisabled) && !(isGpsIncreaseIncreasePriceCode(obj.financeCode)&& isGpsIncreaseItemDisabled)">
                        <Form-item :label="toUpper(obj.financeName)">
                            <span class="spanline32">{{obj.financePrice|amount}}</span>
                        </Form-item>
                    </i-col>
                </Row>
            </Form>
        </div>
    </div>
</template>
<script>
    /**
     * @Description: /GPS//
     */

    import base from './js/loanbase.js';
    //floatMul floatDiv floatAddfloatSub
    import {yx, floatMul, floatDiv, floatAdd, floatSub, transDateToString, stringToNumber} from '@/utils/helper.js';

    //
    const classArr = yx.classArr;

    export default {
        name: 'itemInfo',
        mixins: [base],
        props: {},
        components: {},
        directives: {},
        data() {
            return {}
        },
        computed: {
            //gpsgps
            isGpsIncreaseItemDisabled() {
                let res = yx.isGpsIncreaseItemDisabled(this.orderData.loan)
                return res;
            },
            // 
            isOnline(){
                return this.orderData.loan.baseInfo.custPrimaryChannel === '030100000'? true: false
            },
            // 
            isGift(){
                // 
                // 
                return this.isBuyTheftInsurance && ((this.isOnline &&this.theftInsuranceInfo.isGift == '0')||(!this.isOnline && this.isBuyTheftInsurance))? true: false
            }
        },
        mounted() {
            this.queryDictData(['insuranceTypes']);
            this.queryInitData('itemInfo', () => {
                //
                this.$nextTick(() => {
                    this.todoRecount();
                })
            })
        },
        methods: {
            //,,
            isDisabledItem(obj) {
                let code = obj.financeCode;
                return yx.isCarItemCode(code)
            },
            //GPS
            isGpsIncreaseBasePriceCode(code) {
                return yx.isGpsIncreaseBasePriceCode(code)
            },
            //GPS
            isGpsIncreaseIncreasePriceCode(code) {
                return yx.isGpsIncreaseIncreasePriceCode(code)
            },
            //
            isHasBusiIns(obj) {
                let busiCode = classArr[parseInt(obj.period) - 1][0].dictCode;
                return obj.financeCode.indexOf(busiCode) > -1;
            },
            //
            getInsuranceClassText(vals, i) {
                let arr = [];
                let obj = this.insuranceInfo.insuranceItemList[i];
                vals.forEach((val) => {
                    let row = classArr[parseInt(obj.period) - 1].find(item => item.dictCode == val);
                    if (row) arr.push(row.dictName);
                })
                arr.sort((a, b) => {
                    return a > b ? 1 : -1;
                })
                return arr.join('');
            },
            //
            getInsuranceKindText(vals) {
                let arr = [];
                vals.forEach((val) => {
                    let obj = this.dp['insuranceTypes'].find(item => item.dictCode == val);
                    if (obj) arr.push(obj.dictName);
                })
                return arr.join('');
            },
            //
            yearName(i) {
                let obj = this.insuranceInfo.insuranceItemList[i];
                i = parseInt(obj.period) - 1;//
                return '' + ["", "", "", "", ""][i] + '';
            },
            //
            toUpper(str) {
                return String(str).toUpperCase();
            },
        },
    }
</script>



