<template>
    <div>
        <h3 class="margin-b-14"></h3>
        <Form ref="formModel" :model="formModel" :label-width="120">
            <Row>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.custName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.custCertificateTypeName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.custCertificateNo}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.custPhone}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.custGenderName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.marriageName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.custEducationName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.custDegreeName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.hasEstateName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.livingStatusName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.industryName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.annualSalaryAfterTax|amount}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.custProfessionName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.custPositionName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.custTitleName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.companyNatureName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.currentResidenceYears}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.postalCode}}</span>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.companyName}}</span>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.companyProvinceName}}{{!!formModel.companyCityName?'/':''}}{{formModel.companyCityName}}{{!!formModel.companyDistrictName?'/':''}}{{formModel.companyDistrictName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="16">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.companyAddress}}</span>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.currentResidenceProvinceName}}{{!!formModel.currentResidenceCityName?'/':''}}{{formModel.currentResidenceCityName}}{{!!formModel.currentResidenceDistrictName?'/':''}}{{formModel.currentResidenceDistrictName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="16">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.currentResidenceAddress}}</span>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.censusRegisterProvinceName}}{{!!formModel.censusRegisterCityName?'/':''}}{{formModel.censusRegisterCityName}}{{!!formModel.censusRegisterDistrictName?'/':''}}{{formModel.censusRegisterDistrictName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="16">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.censusRegisterAddress}}</span>
                    </FormItem>
                </i-col>
            </Row>
        </Form>
    </div>
</template>
<script>
    /**
     * @Description: 
     */
    import {
        mapState,
        mapGetters,
        mapMutations,
        mapActions
    } from 'vuex'
    import base from './js/custbase';

    export default {
        name: 'orderPersonalForm',
        mixins: [base],
        props: {},
        components: {},
        data() {
            return {}
        },
        computed: {
            //model
            formModel() {
                return this.orderData.cust.personal;
            },
        },
        created() {
        },
        mounted() {
            this.$nextTick(() => {
                this.todoRecount();
            })
        },
        methods: {},
    }
</script>



