<template>
    <div>
        <h3 class="margin-b-14"></h3>
        <Form ref="formModel" :model="formModel" :label-width="120">
            <Row>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.orderApplicationTypeName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.enterpriseName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.unifiedSocialCreditCode}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.organizationCode}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.organizationCategoryName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.organizationCategoryDetailName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.taxpayerIdentificationNum}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8" v-if="formModel.orderApplicationType=='2'">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.eigenCode}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.countryName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <Form-item label="">
                        <span class="spanline32">{{formModel.isInstitutionName}}</span>
                    </Form-item>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.establishDate}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.enterpriseScaleName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.isLongCertificateName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8" v-if="formModel.isLongCertificate!='1'">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.isLongCertificate}}{{formModel.certificateExpiryDate}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.organizationStatusName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.registeredCapitalCurrencyName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="()">
                        <span class="spanline32">{{formModel.registeredCapital|amount}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.economicTypeName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.economicIndustryLargeClassName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.economicIndustrySmallClassName}}</span>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.enterpriseRegistrationProvinceName}}{{!!formModel.enterpriseRegistrationCityName?'/':''}}{{formModel.enterpriseRegistrationCityName}}{{!!formModel.enterpriseRegistrationDistrictName?'/':''}}{{formModel.enterpriseRegistrationDistrictName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="16">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.enterpriseRegistrationAddress}}</span>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.enterpriseManagementProvinceName}}{{!!formModel.enterpriseManagementCityName?'/':''}}{{formModel.enterpriseManagementCityName}}{{!!formModel.enterpriseManagementDistrictName?'/':''}}{{formModel.enterpriseManagementDistrictName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="16">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.enterpriseManagementAddress}}</span>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="24" :md="24">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.enterpriseBusinessScope}}</span>
                    </FormItem>
                </i-col>
            </Row>
            <Row>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.corporationName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.corporationCertificateTypeName}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.corporationCertificateNo}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.corporationPhone}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="(%)">
                        <span class="spanline32">{{formModel.corporationShareholdingRatio|amount}}</span>
                    </FormItem>
                </i-col>
                <i-col :xs="24" :sm="12" :md="8">
                    <FormItem label="">
                        <span class="spanline32">{{formModel.enterpriseContactPhone}}</span>
                    </FormItem>
                </i-col>
            </Row>
        </Form>
    </div>
</template>
<script>
    /**
     * @Description: 
     */
    import base from './js/custbase';

    export default {
        name: 'orderEnterpriseForm',
        mixins: [base],
        props: {},
        components: {},
        data() {
            return {}
        },
        computed: {
            //
            formModel() {
                return this.orderData.enterprise;
            },
        },
        created() {
        },
        mounted() {
            this.$nextTick(() => {
                this.todoRecount();
            })
        },
        methods: {},
    }
</script>



